<template>
  <v-container ref="boxIds" class="game_child_box">
    <div class="game_child_top">
      <site-map :title="$route.query.gameName" @click-left="doBack" />
      <div class="game_child_search">
        <v-text-field
          v-model="keywords"
          class="search-field input_radius"
          background-color="t_input_light_bg"
          outlined
          :placeholder="$t('General.General_5')"
          :height="loadVariables('search_field_height')"
          hide-details
          single-line
          solo
          flat
          dense
        >
          <template slot="prepend-inner">
            <icon-font class="prepend-icon" name="icon-Icon_Search" :size="loadVariables('icon_search_size')" />
          </template>
        </v-text-field>
      </div>
    </div>
    <!-- 骨架屏 -->
    <space-ui v-if="isSkeleton" sign="grid_list" />
    <template v-else-if="newList.length">
      <game-grid :list="newList.slice(0, pageSize)" @toGame="toGame" />
      <footer class="game_child_pagination">
        <div class="progress_w">
          <v-progress-linear
            color="t_process_dark_piece"
            background-color="t_process_dark_bg"
            rounded
            :value="parseFloat(pageSize / newList.length).toFixed(2) * 100"
          ></v-progress-linear>
        </div>
        <div class="game_child_tips">{{ tips }}</div>
        <button v-if="pageSize < newList.length" class="btn--theme" @click="goNext">
          {{ $t('General.General_8') }}
        </button>
      </footer>
    </template>
    <empty v-else></empty>
  </v-container>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import GameGrid from './ui/game-grid.vue'
import { GRID_LIST_PAGE_SIZE } from '@/api/setting'
import ContainerMixin from '@/components/containerMixin'
import spaceUi from '@/components/skeleton/space-ui.vue'
import Empty from '@/components/ui/empty'
import BackBar from '@/components/ui/back-bar'
import SiteMap from '@/components/ui/site-map'
import variables from '@style/views/menu.index.modules.scss'

export default {
  components: {
    GameGrid,
    spaceUi,
    Empty,
    BackBar,
    SiteMap
  },
  mixins: [ContainerMixin],
  data() {
    return {
      list: [],
      keywords: '',
      newList: [],
      // 弹窗加载游戏
      gameDialog: false,
      gameUrl: '',
      pageSize: GRID_LIST_PAGE_SIZE, // 默认最小值
      isSkeleton: false // 是否显示骨架屏
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user']),
    ...mapState('source', ['pageParameter']),
    tips() {
      if (this.pageSize < this.newList.length) {
        const x = this.newList.length < GRID_LIST_PAGE_SIZE ? this.newList.length : this.pageSize
        const y = this.newList.length
        return this.$t('UI_outros_tip', { X: x, Y: y })
      } else {
        return this.$t('UI_outros_noMore')
      }
    }
  },
  watch: {
    keywords: {
      handler: function (val) {
        this.search(val)
      },
      immediate: true
    }
  },
  created() {
    // this.search(this.keywords)
  },

  methods: {
    ...mapGetters('source', ['getExperienceAuth']),
    loadVariables(name) {
      return variables[name]
    },
    search(val) {
      this.isSkeleton = true

      if (val) {
        this.$u.debounce(() => {
          // 模糊搜索
          const gameList = this.pageParameter?.game_list || []
          this.newList = gameList.filter(el => el.gameName.toUpperCase().indexOf(val.trim().toUpperCase()) >= 0)
        })
      } else {
        this.newList = this.pageParameter.game_list
      }
      setTimeout(() => {
        this.isSkeleton = false
      }, 500)
    },
    toGame(game = {}) {
      this.$store.dispatch('game/enterTheGame', { game, vm: this })
    },
    goNext() {
      if (this.pageSize < this.newList.length) this.pageSize += GRID_LIST_PAGE_SIZE
    },
    doBack() {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';

.game_child_box {
  padding: $lobby_box_pa !important;
  .game_child_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: $featured_top_ma;
    .game_child_search {
      width: $field_search_width;
      .search-field {
        font-size: $search_field_font_size;

        .prepend-icon {
          margin-right: $search_field_prepend_icon_mr;
        }
      }
    }
  }
  .game_child_pagination {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: $featured_pagination_mt;
    .progress_w {
      width: $progress_width;
    }
    .game_child_tips {
      margin: $featured_tips_ma;
      font-size: $featured_tips_font_size;
    }
  }
}

@include breakpoint-only-xs() {
  .game_child_box {
    .game_child_top {
      margin: $xs_featured_top_ma;
      .game_child_search {
        width: 100%;
        margin-top: $xs_field_search_mt;
      }
    }
  }
}
</style>
